import React, { useState, useEffect } from 'react'
import { Card, Row, Col, Table } from 'antd';
import { getPaymentHistory } from '../../../services/payment';
import CardTitle from '../../../components/common/CardTitle/index';
import { dateToString, moneyToString } from '../../../utils/formatUtil';
import './index.scss';

export default () => {
  const [receipts, setReceipts] = useState([]);
  const [isFetching, setIsFetching] = useState(false)

  const callGetPaymentHistory = async () => {
    setIsFetching(true)
    const [result, err] = await getPaymentHistory()
    setIsFetching(false)

    if (err) {
      return;
    }

    setReceipts([...result.data])
  }

  const expandedRowRender = (record) => {
    const nestedTableColumns = [
      {
        title: 'Nội dung',
        dataIndex: 'label',
      },
      {
        title: 'Số tín chỉ',
        dataIndex: 'noOfCredit',
        width: '150px',
        align: 'right'
      },
      {
        title: 'Đơn giá',
        dataIndex: 'unitPrice',
        width: '150px',
        align: 'right',
        render: (text) => text && moneyToString(text)
      },
      {
        title: 'Tổng tiền',
        dataIndex: 'totalAmount',
        width: '150px',
        align: 'right',
        render: (text) => text && moneyToString(text)
      },
      {
        title: 'Thanh toán',
        dataIndex: 'paidAmount',
        width: '150px',
        align: 'right',
        render: (text) => text && moneyToString(text)
      },
    ]
    return <Table className="nested-table" columns={nestedTableColumns} dataSource={record.paymentDetails} pagination={false} />;
  }

  useEffect(() => {
    callGetPaymentHistory()
  }, [])

  const columns = [
    {
      title: 'Năm học',
      dataIndex: 'yearEduLabel',
    },
    {
      title: 'Học kỳ',
      dataIndex: 'semesterLabel',
    },
    {
      title: 'Lần thanh toán',
      dataIndex: 'paymentTimeLabel',
    },
    {
      title: 'Ngày thanh toán',
      dataIndex: 'paymentDate',
      render: (text) => text && dateToString(text)
    },
    {
      title: 'Nội dung',
      dataIndex: 'paymentContent',
    },
    {
      title: 'Hình thức',
      dataIndex: 'paymentTypeLabel',
    },
    {
      title: 'Người thu',
      dataIndex: 'casherName',
    },
    {
      title: 'Giảm',
      dataIndex: 'reducedAmt',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
    {
      title: 'Tổng tiền',
      dataIndex: 'amount',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
    {
      title: 'Còn lại',
      dataIndex: 'remainAmt',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    }
  ]

  return (
    <React.Fragment>
      <Card className="payment-history">
        <CardTitle title="Lịch sử thanh toán" />
        <Row>
          <Col span={24}>
            <Table
              columns={columns}
              dataSource={receipts}
              expandedRowRender={expandedRowRender}
              pagination={false}
              loading={isFetching}
            />
          </Col>
        </Row>
      </Card >
    </React.Fragment>
  )
}


